<template lang="en">
     <section class="main-content">
        <section class="page-action">
            <div class="container-fluid">
                <div class="row">
                    <div class="col-lg-12 mb-3 px-0">
                        <div class="theme-primary partition-full">
                            <span class="p-name text-white"> {{ $t('Manage_Calendar') }}</span>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    <div>
        <div class="m-2">
            <dynamic-form  lang="en" ref="CalendarSource" :mandatorytext="MandatoryString" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
        </div>
        <div class="col-lg-12 mb-3 px-0">
            <div class="heading-border">
              <h5 class="h5"><span>{{ $t('CalendarUrlHeading') }}</span></h5>
            </div>            
         </div>
        <div class="listing m-2 pt-2">
            <div class="table-responsive">
                <table id="Addresslist" class="table table-bordered dt-responsive nowrap mt-0">
                    <thead class="thead-bg">
                        <tr>
                            <th class="text-left"><span>{{ $t('Name') }} </span></th>
                            <th class="text-left"><span>{{ $t('CalendarId') }} </span></th>
                            <th class="text-left"><span>{{ $t('Status') }} </span></th>
                            <th class="text-left"><span>{{ $t('Edit') }} </span></th>
                            <th class="text-left"><span>{{ $t('Delete') }} </span></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="noRecords">
                            <td colspan="5" class="text-danger text-center no-record">{{$t("NoRecordfound")}}</td>
                        </tr>
                        <tr v-else v-for="(data, index) in CalSourceData" :key="ROWNUMBER">
                            <td class="text-left">{{ data.CAL_SOURCE_NAME }}</td>
                            <td class="text-left">{{ data.CAL_SOURCE_URL }}</td>
                            <td class="text-left">{{ data.STATUS_NAME }}</td>
                            <td class="text-left"><em class="enable" data-placement="left" v-on:click="Edit(data)"  href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Edit">
                                <i class="fa fa-pencil text-success action-icon"></i></em>
                            </td>
                            <td class="text-left"><em :class="{'enable':true,'disabled':isDiabled}" data-placement="left" v-on:click="Delete(data)"  href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Delete" aria-describedby="tooltip845041">
                                <i class="fa fa-trash action-icon text-danger"></i></em>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</section>
    </template>
    
    <script>
    import DataService from '../../services/DataService';
    
    export default {
        components: { },
        data() {
            return {
                CalSourceData: [],
                MandatoryString: this.$t('MandatoryString'),
                FormSchema: [{
                    layoutType: "triple",
                    Data: [
                    {
                        astype: "TextField",
                        label: "Name",
                        name: "Name",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            required: true,
                        },
                    },
                    {
                        astype: "TextField",
                        label: "Calendar Id",
                        name: "CalendarId",
                        value: "",
                        placeholder: "",
                        disabled: false,
                        validationRules: {
                            required: true,
                        },
                    },
                    {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "Status",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                    ],
                },
    
                ],
                buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel,
                },
                ],
                SortBy: '',
                SortExp: '',
                noRecords: '',
                PageSize: "10",
                PageNumber: "1",
                CalSourceId: 0
            }
        },
    
        created() {
            var vm = this;
            vm.GetStatusList();
            vm.FetchData();
        },
        methods: {
            FetchData: function () {
                var vm = this;
                vm.isLoading = true;
                var params = `PageNumber=${vm.PageNumber}&pageSize=${vm.PageSize}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
                DataService.GetCalSourceList(params).then(function (response) {
                    if(response.data){
                        vm.CalSourceData = response.data;
                        vm.noRecords=false;
                    }else{
                        vm.noRecords=true;
                    }
                })
            },
            Edit: function (Item) {
                var vm = this;
                (vm.FormSchema)[0].Data[0].value = Item.CAL_SOURCE_NAME;
                (vm.FormSchema)[0].Data[1].value = Item.CAL_SOURCE_URL;
                (vm.FormSchema)[0].Data[2].value = Item.STATUS_ID;
                vm.CalSourceId=Item.CAL_SOURCE_ID;
            },
            GetStatusList: function () {
            var vm = this;
            var url = `statusType=&hasglobal=true&langCode=en`
            DataService.GetCommonStatusList(url).then((response) => {
                if (response.data.length > 0) {
                    var statusData = response.data;
                    statusData.forEach(element => {
                        var Obj = {
                            name: element.STATUS,
                            value: element.STATUS_ID
                        }
                        vm.FormSchema[0].Data[2].config.options.push(Obj);
                    });
                    vm.$refs.CalendarSource.UpdateKeyValue();
                }
            });
            },
            Delete: function (Item) {
                var vm = this;
                vm.Id = Item.CAL_SOURCE_ID;
                if (vm.Id > 0) {
                    vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("source")), function () {
                    var url = `ids=${vm.Id}&tablename=TALYGEN_EM_CAL_SOURCE&primaryKey=cal_source_id`;
                    DataService.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            //vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('CalenderSource')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('source')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
                }
                else {
                    vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                }
                vm.isLoading = false;
            },
            async onSubmit(formfields) {
                var vm = this;
                vm.isLoading = true;
                let myObj = {
                    CalSourceId: vm.CalSourceId,
                    SourceName: formfields.Name,
                    SourceUrl: formfields.CalendarId,
                    Status_id: formfields.Status
                }
                var Jsondata = JSON.stringify(myObj);
                await DataService.AddUpdateCalSource(Jsondata).then(function (response) {
                    if (response != null && response.data != null && response.data == 1) {
                        vm.ShowAlert(vm.$t('RecordsSavedSuccessfully'), 'success');
                        vm.Clear();
                        vm.FetchData();
                    } else if(response != null && response.data != null && response.data == -2){
                        vm.ShowAlert(vm.$t('Calendar_Duplicate_Error'), 'failure');
                    } else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                        vm.FetchData();
                    }
                })
                vm.isLoading = false;
            },
            onCancel: function () {
                this.Clear();
            },
            Clear: function () {
                var vm = this;
                (vm.FormSchema)[0].Data[0].value = '';
                (vm.FormSchema)[0].Data[1].value = '';
                (vm.FormSchema)[0].Data[2].value = '';
                vm.$refs.CalendarSource.validateForm.reset();
            },
        },
    
    }
    </script>
    <style></style>
    